import { useEffect, useState } from "react";
import { GoogleLogin } from "react-google-login";

function Login() {
  const [user, setUser] = useState(null);
  const [userData, setUserData] = useState(null);
  console.log(" userData?.email", userData, user);

  useEffect(() => {
    setUserData({
      email: user?.profileObj?.email,
      givenName: user?.profileObj?.givenName,
      image: user?.profileObj?.imageUrl,
    });
  }, [user]);
  return (
    <div id="signInButton">
      <div>
        {userData?.email}
        {userData?.givenName}
        {userData?.image && <img src={`${userData?.image}`} alt="" />}
      </div>
      <GoogleLogin
        clientId={process.env.REACT_APP_clientId}
        buttonText="Login"
        onSuccess={setUser}
        onFailure={console.log}
        cookiePolicy={"single_host_origin"}
        isSignedIn={true}
        render={renderProps => (
          <button 
            onClick={renderProps.onClick} 
            disabled={renderProps.disabled} 
            style={{
              backgroundColor: '#4285F4',
              color: 'white',
              fontSize: '16px',
              borderRadius: '5px',
              padding: '10px 20px',
              fontWeight: 'bold',
              border: 'none',
              cursor: 'pointer'
            }}
          >
            Login with Google
          </button>
        )}
      />
    </div>
  );
}

export default Login;
